import React, { FC } from 'react';
import { Breadcrumb } from 'antd';
import { useLocation, Link, matchPath } from 'react-router-dom';
import { adminRoutes, trainerRoutes } from '../../router/routes';

interface BreadcrumbsProps {
	role: () => string;
}

interface IBreadcrumb {
	text: string;
	href: string;
}

const playerBreadcrumbs: IBreadcrumb[] = [
	{ text: '–ú–æ–π –î–¶ (Dashboard)', href: '/' },
	{ text: '–ê–≤—Ç–æ', href: '/cars' },
	{ text: '–ü–ª–æ—â–∞–¥–∫–∏', href: '/stands' },
	{ text: '–°–æ—Ç—Ä—É–¥–Ω–∏–∫–∏', href: '/employees' },
	{ text: '–ú–∞—Ä–∫–µ—Ç–∏–Ω–≥', href: '/marketing' },
	{ text: '–ú–∞—Ä–∫–µ—Ç–∏–Ω–≥', href: '/registration' },
	{ text: '–£–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –ø—Ä–æ–¥–∞–≤—Ü–∞–º–∏', href: '/employees/sellers' },
	{ text: '–£–ø—Ä–∞–≤–ª–µ–Ω–∏–µ —É–Ω–∏–≤–µ—Ä—Å–∞–ª–∞–º–∏', href: '/employees/stationSellers' },
	{ text: '–£–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –æ—Ü–µ–Ω—â–∏–∫–∞–º–∏–∏', href: '/employees/appraisers' },
];

const Breadcrumbs: FC<BreadcrumbsProps> = ({ role }) => {
	const router = useLocation();

	let breadcrumbs: IBreadcrumb[] = [];

	if (role() === 'TRAINER') {
		breadcrumbs = trainerRoutes.map(({ path, name }) => ({
			text: name,
			href: path,
		}));
	} else if (role() === 'ADMIN') {
		breadcrumbs = adminRoutes.map(({ path, name }) => ({
			text: name,
			href: path,
		}));
	} else {
		breadcrumbs = playerBreadcrumbs;
	}

	const PathName = () => {
		const path = breadcrumbs.find(item => item.href === router.pathname);
		const pathName = path ? path.text : getNameFromRouteProps();
		return <span>{pathName}</span>;
	};

	const getNameFromRouteProps = () => {
		const currentRoute = [...adminRoutes, ...trainerRoutes].find(route =>
			matchPath(router.pathname, route)
		);
		return currentRoute?.name || '–ù–µ–∏–∑–≤–µ—Å—Ç–Ω–æ';
	};

	return (
		<Breadcrumb style={{ margin: '16px 0' }}>
			<Breadcrumb.Item>
				<Link to='/'>–ì–ª–∞–≤–Ω–∞—è</Link>
			</Breadcrumb.Item>
			<Breadcrumb.Item>{PathName()}</Breadcrumb.Item>
		</Breadcrumb>
	);
};

export default React.memo(Breadcrumbs);
