import React, { useEffect, useState } from 'react';
import { Alert, Button, Form, Input } from 'antd';
import { useAppDispatch, useAppSelector } from '../../app/hooks';
import { fetchTrainers, setFromVisible, setTrainer } from '../../feature/admin/adminSlice';
import { RegAdmin } from '../../types/admin';
import AuthService from '../../services/AuthService';

interface IData {
	login: string;
	name: string;
	password: string;
	confirmPassword: string;
}

const TrainerRegisterForm = () => {
	const dispatch = useAppDispatch();
	const { formIsVisible } = useAppSelector(state => state.admin);
	const [form] = Form.useForm();
	const [error, setError] = useState<string>('');
	const [isLoading, setIsLoading] = useState<boolean>(false);

	useEffect(() => {
		if (!formIsVisible) {
			form.resetFields();
			setError('');
		}
	}, [formIsVisible]);

	const onSubmit = async (data: IData) => {
		if (data.password !== data.confirmPassword) return;
		const newTrainer: RegAdmin = data;
		setIsLoading(true);
		try {
			const response = await AuthService.registrationTrainer(newTrainer);
			form.resetFields();
			dispatch(setTrainer(response.data));
			dispatch(fetchTrainers());
			dispatch(setFromVisible(false));
		} catch (e) {
			console.log('error of login', e.response?.data?.message);
			setError(e.response?.data?.message || '–û—à–∏–±–∫–∞ –∑–∞–ø—Ä–æ—Å–∞');
		} finally {
			setIsLoading(false);
		}
	};

	const onFinish = (values: any) => {
		console.log('Received values of form: ', values);
		onSubmit(values);
	};

	const onFormChange = () => {
		setError('');
	};

	const formItemLayout = {
		labelCol: {
			xs: { span: 24 },
			sm: { span: 10 },
		},
		wrapperCol: {
			xs: { span: 24 },
			sm: { span: 14 },
		},
	};

	const tailFormItemLayout = {
		wrapperCol: {
			xs: {
				span: 24,
				offset: 8,
			},
			sm: {
				span: 14,
				offset: 9,
			},
		},
	};

	return (
		<>
			{error !== '' && (
				<div className='login__alert'>
					<Alert message='–í–Ω–∏–º–∞–Ω–∏–µ' type='error' showIcon description={error} />
				</div>
			)}
			<Form
				{...formItemLayout}
				form={form}
				name='register'
				onFinish={onFinish}
				onChange={onFormChange}
				scrollToFirstError
			>
				<Form.Item
					name='login'
					label='–õ–æ–≥–∏–Ω'
					rules={[
						{
							required: true,
							message: '–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è',
						},
						() => ({
							validator(_, value) {
								if (/^[a-zA-Z0-9]+$/gm.test(value)) {
									return Promise.resolve();
								}
								return Promise.reject(
									new Error('–î–æ–ø—É—Å–∫–∞—é—Ç—Å—è —Ç–æ–ª—å–∫–æ –ª–∞—Ç–∏–Ω—Å–∫–∏–µ —Ü–∏—Ñ—Ä—ã –∏ –±—É–∫–≤—ã –±–µ–∑ –ø—Ä–æ–±–ª–µ—å–Ω—ã—Ö —Å–∏–º–æ–≤–æ–ª–æ–≤')
								);
							},
						}),
					]}
				>
					<Input disabled={isLoading} />
				</Form.Item>
				<Form.Item
					name='name'
					label='–ò–º—è'
					tooltip='–û—Ç–æ–±—Ä–∞–∂–µ–º–æ–µ –∏–º—è –¥–ª—è –¥—Ä—É–≥–∏—Ö –∏–≥—Ä–æ–∫–æ–≤'
					rules={[{ required: true, message: '–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è', whitespace: true }]}
				>
					<Input />
				</Form.Item>
				<Form.Item
					name='password'
					label='–ü–∞—Ä–æ–ª—å'
					rules={[
						{
							required: true,
							message: '–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è',
						},
					]}
					hasFeedback
				>
					<Input.Password disabled={isLoading} />
				</Form.Item>
				<Form.Item
					name='confirmPassword'
					label='–ü–æ–≤—Ç–æ—Ä–∏—Ç–µ –ø–∞—Ä–æ–ª—å'
					dependencies={['password']}
					hasFeedback
					rules={[
						{
							required: true,
							message: '–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è',
						},
						({ getFieldValue }) => ({
							validator(_, value) {
								if (!value || getFieldValue('password') === value) {
									return Promise.resolve();
								}
								return Promise.reject(new Error('–ü–∞—Ä–æ–ª–∏ –Ω–µ —Å–æ–≤–ø–∞–¥–∞—é—Ç!'));
							},
						}),
					]}
				>
					<Input.Password disabled={isLoading} />
				</Form.Item>
				<Form.Item {...tailFormItemLayout}>
					<Button loading={isLoading} type='primary' htmlType='submit' style={{ marginTop: 10 }}>
						–î–æ–±–∞–≤–∏—Ç—å —Ç—Ä–µ–Ω–µ—Ä–∞
					</Button>
				</Form.Item>
			</Form>
		</>
	);
};

export default TrainerRegisterForm;
