import React, { useState } from 'react';
import { Alert, Button, Form, Input } from 'antd';
import { useForm } from 'react-hook-form';
import { useAppDispatch } from '../../app/hooks';
import { checkAuth } from '../../feature/player/playerSlice';
import { LoginAdmin } from '../../types/admin';
import AuthService from '../../services/AuthService';

const AdminLoginForm = () => {
	const { reset } = useForm();
	const dispatch = useAppDispatch();
	const [error, setError] = useState<string>('');
	const [isLoading, setIsLoading] = useState<boolean>(false);

	const onFinish = async (values: any) => {
		const userData: LoginAdmin = {
			...values,
		};
		setIsLoading(true);
		try {
			const response = await AuthService.loginAdminOrTrainer(userData);
			localStorage.setItem('token', response.data.accessToken);
			reset();
			dispatch(checkAuth());
		} catch (e) {
			console.log('error of login', e.response?.data?.message);
			setError(e.response?.data?.message || '–û—à–∏–±–∫–∞ –∑–∞–ø—Ä–æ—Å–∞');
		} finally {
			setIsLoading(false);
		}
	};

	const onFinishFailed = (errorInfo: any) => {
		console.log('Failed:', errorInfo);
	};

	const onFormChange = () => {
		setError('');
	};

	return (
		<>
			{error !== '' && (
				<div className='login__alert'>
					<Alert message='–í–Ω–∏–º–∞–Ω–∏–µ' type='error' showIcon description={error} />
				</div>
			)}
			<Form
				name='basic'
				labelCol={{ span: 6 }}
				wrapperCol={{ span: 16 }}
				initialValues={{ remember: true }}
				onFinish={onFinish}
				onFinishFailed={onFinishFailed}
				onChange={onFormChange}
			>
				<Form.Item
					label='–õ–æ–≥–∏–Ω'
					name='login'
					rules={[{ required: true, message: '–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è' }]}
					labelAlign='left'
				>
					<Input disabled={isLoading} />
				</Form.Item>

				<Form.Item
					label='–ü–∞—Ä–æ–ª—å'
					name='password'
					rules={[{ required: true, message: '–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è' }]}
					labelAlign='left'
				>
					<Input.Password disabled={isLoading} />
				</Form.Item>

				<Form.Item wrapperCol={{ offset: 6, span: 12 }}>
					<Button loading={isLoading} type='primary' htmlType='submit'>
						–í–æ–π—Ç–∏
					</Button>
				</Form.Item>
			</Form>
		</>
	);
};

export default AdminLoginForm;
